@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  transition: all 0.4s ease-in;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 1rem;
}

button:focus {
  outline: none;
}

button > * {
  pointer-events: none;
}

body {
  background-color: #020509;
  font-family: 'IBM Plex Sans', sans-serif;
  margin: 0 auto;
  padding: 2em;
  color: #fff;
  height: 100vh;
  transition: 0.4s ease;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 100;
  background-color: #020509;
  width: 96vw;
  padding: 1em 0;
}

.logo {
  margin: 0 1rem 0 0;
  width: 5rem;
  padding: 0.125rem 0.5rem;
  cursor: pointer;
}

.app-main {
  margin-top: 4rem;
}

.search {
  background-color: transparent;
  border: 2px solid #e1e2e2;
  border-radius: 50px;
  font-family: inherit;
  padding: 0.5rem 1rem;
  color: #fff;
}

.search::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.search:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.search::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.search::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.search:focus {
  outline: none;
}

.app-header--nav {
  display: flex;
  flex: 1 1;
}

.app-header--mobile {
  display: none;
}

.section-pagination {
  background-color: #e1e2e2;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.card-group {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

.container h2 {
  font-size: 1.5rem;
}

.card-group--content {
  padding: 1rem 0.5rem;
  width: 14%;
}

@media (max-width: 1024px) {
  .card-group--content {
    width: 16.5%;
  }

  .app-header--mobile .search {
    margin: 1rem;
  }
}

@media (max-width: 768px) {
  body {
    padding: 1em;
  }

  .app {
    padding-bottom: 60px;
  }

  .app-header {
    display: none;
  }

  .app-header--mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .app-header--mobile-nav {
    display: none;
  }

  .search {
    width: 80vw;
  }

  .app-main {
    margin: 0;
  }

  .card-group--content {
    width: 25%;
  }
}

.mobile-menu {
  max-width: 100%;
  width: 100%;
  z-index: 100;
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: #020509;
  display: flex;
  height: 3.5rem;
  justify-content: center;

  box-shadow: 0px 0 10px rgba(0, 0, 0, 0.5);
  /* border-top: rgba(255, 255, 255, 0.25) solid 1px; */
}

.mobile-menu button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.5);
  flex: 1 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mobile-menu .active:hover {
  color: #fff;
  font-weight: 700;
}

.mobile-menu button:active {
  background-color: transparent;
  color: #fff;
}

.mobile-menu button span {
  font-size: 0.75rem;
  letter-spacing: 0.02rem;
  margin: 0.5rem 0 0;
}

.card-info small,
.overview small {
  font-size: 0.75rem;
}

.card {
  background-color: #fff;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  border-radius: 3px;
}

.card img {
  background-color: #020509;
  width: 100%;
}

.card-info {
  color: #020509;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 1rem 0.75rem 1rem;
  letter-spacing: 0.5px;
}

.card-info small,
.card h3 {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.overview {
  background-color: #fff;
  padding: 1rem 1.1rem;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  max-height: 100%;
  transform: translateY(101%);
  overflow-y: auto;
  transition: transform 0.3s ease-in;
  color: #020509;
  cursor: default;
}

.overview button {
  display: none;
}

.overview p {
  margin: 1em 0 0;
}

.card:hover .overview {
  transform: translateY(0);
}

@media (max-width: 768px) {
  .card-info {
    padding: 0.2em;
  }

  .overview {
    bottom: 45px;
    padding: 1.5em;
    position: fixed;
    z-index: 1;
  }

  .overview button {
    position: absolute;
    right: 1rem;
    top: 1rem;
    display: block;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
  }

  .card h3 {
    font-size: 0.75rem;
  }

  .overview h3 {
    font-size: 1.125rem;
  }
}

.watching-container {
  width: 100%;
  height: 100%;
  margin: 2rem 0;
}

.watching--backdrop {
  position: relative;
  height: 25em;
  background-size: auto;
  background-position: top right;
  background-repeat: no-repeat;
  overflow: hidden;
  border-radius: 3px;
}

.backdrop--info-mobile {
  display: none;
}

.backdrop--info {
  background: rgb(0, 0, 0);
  /* background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.3)); */
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 1) 25%,
    rgba(0, 0, 0, 0.75) 50%,
    rgba(0, 0, 0, 0.25)
  );
  color: #f1f1f1;
  width: 100%;
  height: 100%;
  display: flex;
  padding-left: 2.5em;
  justify-content: center;
  flex-direction: column;
}

.backdrop--info-title {
  font-size: 3.75em;
}

.backdrop--info-subtitle {
  font-size: 0.75em;
  color: rgba(255, 255, 255, 0.75);
}

.backdrop--info-overview {
  margin-top: 0.5em;
  max-width: 35%;
}

@media (max-width: 800px) {
  .backdrop--info {
    display: none;
  }

  .watching-container {
    margin: 1rem 0;
  }

  .backdrop--info-mobile {
    display: block;
  }

  .watching--backdrop {
    height: 10em;
    background-size: cover;
  }

  .backdrop--info-title {
    font-size: 1.25rem;
    margin: 0.25em 0;
  }

  .backdrop--info-overview {
    width: 100%;
    max-width: 100%;
  }
}

.menu {
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu button {
  text-transform: uppercase;
  cursor: pointer;
  padding: 0 1rem;
  background-color: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.5);
  height: 40px;
  letter-spacing: 0.05em;
  font-size: 0.875rem;
}

.menu .active {
  color: #fff;
  font-weight: 700;
}

.menu button:hover {
  color: #fff;
  background-color: #ff0038;
  border-radius: 5rem;
}

.active:hover {
  color: #fff;
}

